import Vue from 'vue'

export const gameStore = new Vue({
    data: {
        gameState: {
            isNewGame: false,
            characterCreated: false,
            turnCounter: 1,
            madeCreatures: 0,
            enemyUnits: [],
            
        },
        playerCharacter: {},
        eventLog: [],
    }
})

export const unitsOfOrder = new Vue ({
    data: {

    },
    methods: {
        UnitOfOrder(name, hp, maxHp, dmg, imgUrl, initiative, owner, absorption, regeneration, value, position) {
            this.name= name;
            this.hp = hp;
            this.maxHp = maxHp;
            this.dmg = dmg;
            this.imgUrl = imgUrl;
            this.initiative = initiative;
            this.owner = owner;
            this.absorption = absorption;
            this.regeneration = regeneration;
            this.value = value;
            this.position = position;
            },
        createPeasant() {
            let peasant = new this.UnitOfOrder("Peasant", 4, 4, 1, "peasant.svg", 1, "enemy", 0, 0, 1, 1);
            return peasant;
            },
        createBlacksmith() {
            let blacksmith = new this.UnitOfOrder("Blacksmith", 10, 10, 3, "blacksmith.svg", 1, "enemy", 0, 0, 3, 1);
            return blacksmith;
        }
    }
    
})